import React, { FC } from 'react';
import { CommonProps } from '@interface';
import { ItemClickType } from '@enum';
import './index.less';
import classNames from 'classnames';

const AppIcon: FC<CommonProps> = (props = {}) => {
    const {
        schema: {
            props: { size = 'middle' } = {},
            logInfo: { itemClickType = ItemClickType.CARD_ICON_CLICK } = {},
        },
        style = {},
        info: { adAppUrl = 'https://static.yximgs.com/udata/pkg/ad-universe/h5_default_icon.png' },
    } = props;

    return adAppUrl ? (
        <img
            data-itemClickType={itemClickType}
            src={adAppUrl}
            className={classNames('app-icon', {
                'size-58': size === 'small',
                'size-80': size === 'middle',
                'size-100': size === 'large',
            })}
            style={style}
        />
    ) : null;
};

export default AppIcon;
