import React, { useCallback } from 'react';
import lottie, { AnimationItem } from 'lottie-web/build/player/lottie_svg';
import touchId from './touchid.json';
import touchEnd from './touch-end.json';
import './index.less';

const TouchId = ({ isTouched }) => {
  let touchIdAnim: AnimationItem;
  let touchEndAnim: AnimationItem;

  const touchIdEl = useCallback(
    (node) => {
      if (node === null || isTouched) {
        touchIdAnim?.destroy && touchIdAnim.destroy();
        return;
      }
      touchIdAnim = lottie.loadAnimation({
        container: node,
        renderer: 'svg',
        animationData: touchId,
        loop: true,
        autoplay: false,
      });
      setTimeout(() => {
        touchIdAnim.play();
      }, 500);
      touchIdAnim.setSubframe(false);
    },
    [isTouched],
  );
  const touchEndEl = useCallback((node) => {
    if (node === null) return;
    touchEndAnim = lottie.loadAnimation({
      container: node,
      renderer: 'svg',
      animationData: touchEnd,
      loop: false,
      autoplay: false,
    });
    touchEndAnim.play();
    touchEndAnim.setSubframe(false);
  }, []);

  return !isTouched ? (
    <div className="touch-id">
      <div ref={touchIdEl}></div>
    </div>
  ) : (
    <div className="touch-end">
      <div ref={touchEndEl}></div>
    </div>
  );
};
export default TouchId;
