import { CommonProps } from '@interface';
import classNames from 'classnames';
import { FC } from 'react';
import './index.less';

const Container: FC<CommonProps> = (props = {}) => {
  const {
    schema: {
      className = '',
      props: {
        display = 'flex',
        direction = 'column', // row | column
        align = 'center',
        justify = 'center',
      } = {},
      children = [],
      logInfo: { itemClickType = '' } = {},
    },
    style = {},
    render,
  } = props;

  return (
    <div
      className={classNames(
        'container',
        display === 'flex' ? ['flex', direction, `align-${align}`, `justify-${justify}`] : null,
        className,
      )}
      style={style}
      data-itemClickType={itemClickType}
    >
      {Array.isArray(children) && children.length
        ? children.map((item) => {
            return render({ node: item });
          })
        : null}
    </div>
  );
};

export default Container;
