import '@assets/styles/animate.less';
import '@assets/styles/global.less';
import { ItemClickType } from '@enum';
import { CommonProps } from '@interface';
import classNames from 'classnames';
import { RootStoreContext } from 'context/StoreContext';
import { FC, useContext } from 'react';
import Guide from './Guide';
import './index.less';

const Root: FC<CommonProps> = (props = {}) => {
  const {
    schema: { blurRadius = 0, justify = 'center', children = [] },
    style = {},
    info: { isPorttrait, materialFeature = [] },
    render,
  } = props;
  const backgrounds: string[] = materialFeature.map((item) => `url(${item.coverUrl})`);

  const { page: { isBg = true, isMask = false, maskColor = 'transparent', isGuide = false } = {} } =
    useContext(RootStoreContext);

  const bgStyle = {
    backgroundImage: backgrounds[0],
    backgroundSize: 'cover',
    backgroundRepeat: 'no-repeat',
    filter: `blur(${blurRadius}px)`,
  };
  const landscapeRepeatList = new Array(3).fill(0);
  const imageSize = materialFeature[0]?.materialSize;
  const isHorizontal = imageSize.width > imageSize.height;

  // 横版并且图片是长图，则需要平铺3张
  const isMulti = !isPorttrait && !isHorizontal;

  // 如果是竖屏，并且是水平图片，则需要居中展示
  if (isPorttrait && isHorizontal) {
    bgStyle.backgroundSize = 'contain';
  }

  return (
    <div
      className={classNames('layout', `justify-${justify}`, {
        'flex-row': !isPorttrait,
        'flex-column': isPorttrait,
      })}
      style={style}
      data-itemClickType={ItemClickType.BLANK_AREA_CLICK}
    >
      {/* 背景 */}
      {isBg ? (
        <div className="background-wrap" data-itemClickType={ItemClickType.BLANK_AREA_CLICK}>
          {!isMulti && <div className="background-porttrait" style={{ ...bgStyle }} />}
          {isMulti &&
            landscapeRepeatList.map((_item, i) => {
              return <div key={i} className="background-landscape" style={{ ...bgStyle }} />;
            })}
          {isMask && <div className="background-cover" style={{ backgroundColor: maskColor }} />}
        </div>
      ) : null}
      <>
        {Array.isArray(children) && children.length
          ? children.map((item) => {
              return render({ node: item });
            })
          : null}
      </>
      <Guide isShow={isGuide} isPorttrait={isPorttrait} render={render}></Guide>
    </div>
  );
};
export default Root;
