import { ItemClickType } from '@enum';
import { CommonProps } from '@interface';
import { FC } from 'react';
import { styleHandle } from 'utils';
import './index.less';

const DownloadCount: FC<CommonProps> = (props = {}) => {
    const {
        schema: {
            className = '',
            logInfo: { itemClickType = ItemClickType.CARD_APP_DOWNLOAD_NUM } = {},
            props: { color = '', fontSize = '' } = {},
        },
        style = {},
        info: { appDownloadCountDesc = '', isDownload },
    } = props;
    return appDownloadCountDesc && isDownload ? (
        <div
            data-itemClickType={itemClickType}
            className="download-count line-1 size-14"
            style={styleHandle(style, { color, fontSize })}
        >
            {appDownloadCountDesc}
        </div>
    ) : null;
};

export default DownloadCount;
