import { ItemClickType } from '@enum';
import { CommonProps } from '@interface';
import { FC } from 'react';
import { styleHandle } from 'utils';
import './index.less';

const Des: FC<CommonProps> = (props = {}) => {
  const {
    schema: {
      text,
      logInfo: { itemClickType = ItemClickType.CARD_DES_CLICK } = {},
      props: { color = '', fontSize = '' } = {},
    },
    style = {},
    info: { adDescription = '' },
  } = props;
  const desc = text || adDescription;
  return desc ? (
    <div
      data-itemClickType={itemClickType}
      className="des line-2 size-16"
      style={styleHandle(style, { color, fontSize })}
    >
      {desc}
    </div>
  ) : null;
};

export default Des;
