const isProd = process.env.NODE_ENV === 'production';
export const radarFmp = () => {
  try {
    window.weblog.radarFmp();
  } catch (e) {}
};

export function radarInfo(
  message: string,
  extra: {
    args?: { [key: string]: any };
    event_type?: string;
    event_count?: number;
    duration?: number;
  } = {},
) {
  try {
    if (isProd) {
      const _extra = extra || {};
      if (extra.duration) {
        _extra.duration = Math.floor(extra.duration);
      }
      window.weblog.radarInfo(message, _extra);
    } else {
      console.log(message, extra);
    }
  } catch (e) {}
}

export function radarStart(name: string, extra_info?: string) {
  try {
    window.weblog.radarStart(name, extra_info);
  } catch (e) {}
}

export function radarEnd(name: string, extra_info?: string, result_type?: string) {
  try {
    window.weblog.radarEnd(
      name,
      extra_info, // 【可选】
      result_type, // 【可选】 事件结果
    );
  } catch (e) {}
}

export function radarCustomStage(key: string) {
  try {
    window.weblog.radarCustomStage(key);
  } catch (e) {}
}

export function setDimensions(params) {
  try {
    window.weblog.setDimensions(params);
  } catch (error) {}
}
