import { ksbLog } from '@api';
import { bridge } from '@bridge';
import { CommonProps } from '@interface';
import classNames from 'classnames';
import { FC } from 'react';
import './index.less';

const Button: FC<CommonProps> = (props = {}) => {
  const {
    schema: {
      className = '',
      text = '强制关闭',
      props: {
        size = 'middle', // small middle large
        shape = 'circle', // default circle round
        block = 'normal', // normall  full
      } = {},
      logInfo: { itemClickType = '' } = {},
    },
    style = {},
  } = props;

  return (
    <button
      type="button"
      data-itemClickType={itemClickType}
      className={classNames(
        'close-btn',
        `size-${size}`,
        { block: block === 'full' },
        { [`shape-${shape}`]: !!shape },
        className,
      )}
      onTouchEnd={(e) => {
        // todo: 关闭打点 trace.ts
        bridge?.close();
        ksbLog({
          actionType: 3,
        });
        e.stopPropagation();
      }}
      style={style}
    >
      <span>{text}</span>
    </button>
  );
};

export default Button;
