/**
 * 提供原生api
 */
export const ajax = function (options: { [key: string]: any }) {
  return new Promise((resolve, reject) => {
    options = options || {};
    options.type = (options.type || 'GET').toUpperCase();
    // 响应数据格式，默认json
    options.dataType = options.dataType || 'json';

    const params = JSON.stringify(options.data) || '';

    const xhr: any = new XMLHttpRequest();

    if (options.type === 'GET') {
      xhr.open('GET', `${options.url}?${params}`, true);
      xhr.send(null);
    } else if (options.type === 'POST') {
      xhr.open('POST', options.url, true);
      xhr.setRequestHeader('Content-type', 'application/json');
      xhr.setRequestHeader('trace-context', '{ laneId:ad_test.test0098.yanliang}');
      xhr.send(params);
    }

    // 设置超时
    setTimeout(function () {
      if (xhr.readySate !== 4) {
        xhr.abort();
      }
    }, options.timeout || 10000);

    xhr.onreadystatechange = function () {
      if (xhr.readyState === 4) {
        const { status } = xhr;
        if ((status >= 200 && status < 300) || status === 304) {
          let res = xhr.responseText;
          if (options.dataType === 'json') {
            try {
              res = JSON.parse(res);
            } catch (error) {
              // tslint:disable-next-line: no-console
              console.log(error);
            }
          }
          resolve({
            res,
            responseXML: xhr.responseXML,
          });
        } else {
          reject(status);
        }
      }
    };
  });
};

export const getQueryString = (name) => {
  try {
    const reg = new RegExp(`(^|&)${name}=([^&]*)(&|$)`, 'i');
    const r = window.location.search.substring(1).match(reg);

    if (r != null) {
      return unescape(r[2]);
    }
    return null;
  } catch (e) {
    return null;
  }
};

export const setQueryString = (name, value) => {
  const url = window.location.search;
  let resultUrl = '';
  const reg = new RegExp(`(^|&)${name}=([^&]*)(&|$)`, 'i');
  const r = window.location.search.substr(1).match(reg);
  const replaceStr = `${name}=${value}`;
  if (r != null) {
    const tmp = url.replace(`${name}=${r[2]}`, replaceStr);
    resultUrl = tmp;
  } else if (url.match('[?]')) {
    resultUrl = `${url}&${replaceStr}`;
  } else {
    resultUrl = `${url}?${replaceStr}`;
  }
  window.history.replaceState(null, null, resultUrl);
};

export const isMock =
  getQueryString('env') === 'mock' ||
  process.env.NODE_ENV === 'development' ||
  window.navigator.userAgent.includes('Macintosh; Intel Mac OS X');
