import { ItemClickType } from '@enum';
import { CommonProps } from '@interface';
import { FC } from 'react';
import { styleHandle } from 'utils';
import './index.less';

const Title: FC<CommonProps> = (props = {}) => {
  const {
    schema: {
      props: {
        size = 'middle', // small middle large
        line = 1, // 一行or两行
        color = '#222222',
        fontSize = '24',
      } = {},
      logInfo: { itemClickType = ItemClickType.CARD_TITLE_CLICK } = {},
    },
    style = {},
    info: { adTitle = '' },
  } = props;

  const titleStyle = styleHandle(style, { color, fontSize });
  return adTitle ? (
    <div
      data-itemClickType={itemClickType}
      className={`title size-${size} line-${line}`}
      style={titleStyle}
    >
      {adTitle}
    </div>
  ) : null;
};

export default Title;
