import NormalStar from '@component/icons/NormalStar';
import SharpStar from '@component/icons/SharpStar';
import SmileStar from '@component/icons/SmileStar';
import { ItemClickType } from '@enum';
import { CommonProps } from '@interface';
import { scoreCeil } from '@util';
import { FC } from 'react';
import { styleHandle } from 'utils';
import './index.less';

const Star: FC<CommonProps> = (props = {}) => {
  const {
    schema: {
      props: {
        type = 'normal', // normal, sharp, smaile
        isShowScore = false,
        color = '#FE3666',
        fontSize = 14,
        offset = 5,
        count = 5,
      } = {},
      logInfo: { itemClickType = ItemClickType.PLAYEND_APP_SCORE_CLICK } = {},
    },
    style = {},
    info: { appScore = 0, isDownload },
  } = props;

  const value = scoreCeil(Number(appScore) / 10);
  const starList = new Array(count).fill(0);
  const showDecimalIcon = (item: number) => {
    item += 1;
    const isHalf = item - 1 < value && item > value;
    return isHalf;
  };
  const hexToRGB = function (hex: string, alpha: number) {
    var r = parseInt(hex.slice(1, 3), 16),
      g = parseInt(hex.slice(3, 5), 16),
      b = parseInt(hex.slice(5, 7), 16);

    if (alpha) {
      return 'rgba(' + r + ', ' + g + ', ' + b + ', ' + alpha + ')';
    } else {
      return 'rgb(' + r + ', ' + g + ', ' + b + ')';
    }
  };

  let IconStar;
  switch (type) {
    case 'sharp':
      IconStar = SharpStar;
      break;
    case 'smaile':
      IconStar = SmileStar;
      break;

    default:
      IconStar = NormalStar;
      break;
  }

  const isOnStar = (item) => {
    item += 1;
    return item <= value;
  };

  const offColor = hexToRGB(color, 0.3);

  if (!isDownload) {
    return;
  }

  return (
    <>
      {isDownload && (
        <div className="star-group" data-itemClickType={itemClickType} style={style}>
          {starList.map((_, i) => {
            return (
              <span className="star" style={styleHandle({ marginRight: offset })}>
                <i className="star-icon">
                  <IconStar size={fontSize} color={isOnStar(i) ? color : offColor} />
                  {showDecimalIcon(i) && (
                    <i className="star-icon-decimal" style={{ width: '50%' }}>
                      <IconStar size={fontSize} color={color} />
                    </i>
                  )}
                </i>
              </span>
            );
          })}
          {isShowScore ? (
            <div className="appScoreVal" style={styleHandle({ color, fontSize: fontSize + 2 })}>
              {value}
            </div>
          ) : null}
        </div>
      )}
    </>
  );
};

export default Star;
