export enum AdActionType {
    AD_CARD_CLOSE = 243, // 卡片关闭事件
    AD_CARD_IMPRESSION = 241, // 卡片曝光
    AD_ITEM_CLICK = 2,
    AD_ELEMENT_CLICK = 141,
    AD_ITEM_PLAY_END_CARD_IMPRESSION = 531, // 播放结束页通用卡片曝光
    AD_PHOTO_REPLAYED = 24, // 重播点击
    AD_ITEM_CLOSE = 3, // 关闭
    AD_ELEMENT_IMPRESSION = 140, // 标识广告元素的曝光，配合参数ElementType 标识具体的广告元素
    AD_ITEM_NEGATIVE = 4, // 负反馈(内容联盟卡片关闭)
    ACT_AD_AGG_PAGE_REFRESH = 12006, // 聚合广告页面刷新
    ACT_ITEM_IMPRESSION = 1, // 图文素材曝光
    AD_ITEM_IMPRESSION = 1, // 素材曝光
    AD_ITEM_SKIPED = 501, // 视频跳过
}

export enum ElementType {}

export enum ItemClickType {
    CAED_SKIDE_CLICK = 153, // 卡片滑动点击上报参数
    PLAY_END_ICON_CLICK = 15, // 播放结束页点击icon跳转落地页
    PLAY_END_ICON_TITLE_CLICK = 16, // 播放结束页点击icon标题跳转落地页
    PLAYEND_APP_DOWNLOAD_NUM = 44, // 播放结束页下载次数元素
    CARD_APP_SCORE_CLICK = 45, // 通用卡片评审元素
    BLANK_AREA_CLICK = 35, // 任意背景位点击

    END_CARD_BTN = 26, // 联盟播放结束页单button
    PLAY_END_CLICK1 = 37, // 联盟播放结束页双button--左侧点击按钮
    PLAY_END_CLICK2 = 38, // 联盟播放结束页双button-右侧点击按钮

    // 埋点统一
    CARD_ACTION_BAR_CLICK = 29, // 用户成功点击卡片上的actionbar跳转到落地页时即可触发
    CARD_BLANK_CLICK = 53, // 空白区域点击
    CARD_ICON_CLICK = 30, // 用户成功点击卡片里的app头像跳转到落地页时即可触发
    CARD_TITLE_CLICK = 31, // 用户成功点击卡片里的app名称跳转到落地页时即可触发
    CARD_DES_CLICK = 32, // 用户成功点击卡片里的描述信息跳转到落地页时即可触发
    CARD_APP_DOWNLOAD_NUM = 46, // 通用卡片下载次数元素
    PLAYEND_APP_SCORE_CLICK = 27, // 击播放结束页点击app评分
}

export const traceToAreaMap = (itemClickType: string) => {
    const map = {
        [ItemClickType.CARD_ACTION_BAR_CLICK]: 1,
        [ItemClickType.PLAY_END_CLICK1]: 1,
        [ItemClickType.PLAY_END_CLICK2]: 1,
        [ItemClickType.END_CARD_BTN]: 1,
    };
    return map[itemClickType] ? map[itemClickType] : 2;
};
