import { changePageStatus, fetchData, ksbLog, clientLog } from '@api';
import {
  getQueryString,
  isLocal,
  isMock,
  radarEnd,
  radarFmp,
  radarStart,
  setQueryString,
  setDimensions,
  radarInfo,
} from '@util';
import {
  PageStatus,
  AdActionType,
  ClientEvent,
  AdStyle,
  PageType,
  ClientStatus,
} from 'common/enum';
import { render } from 'react-dom';
import { Root } from './index';
import { bridge } from '@bridge';

const main = async () => {
  let json: any = {};
  // env环境 mock | local
  if (isMock) {
    // mock | local
    // eslint-disable-next-line import/no-dynamic-require
    json = require('../mock/template0VH.json');
  } else {
    // 线上环境
    const matrixType = getQueryString('matrixType') || 'actionBarInfo';
    json = { matrixType };
  }
  const data: any = (await fetchData({ ...json })) || {};
  const { info = {} } = data;
  let { matrixLayout } = info; // 获取布局

  if (!matrixLayout) {
    // 布局不存在时 异常处理
    changePageStatus(PageStatus.ERROR); // 渲染失败
    return;
  }

  if (isLocal) {
    matrixLayout = json;
  } // local 环境 使用本地json

  const { layout = {}, components = [], page } = matrixLayout || {}; // 获取容器布局信息

  console.log(info, 'info======');
  if (isMock) {
    console.log(json, 'layout======');
  }
  // 新增样式维度
  setDimensions({
    c_dimension1: matrixLayout.styleId,
  });

  /**
   * 加载完成监控埋点
   */
  try {
    clientLog({
      event: ClientEvent.PAGE_LOAD,
      scene_id: AdStyle[info.adStyle],
      page_type: PageType.ENDCARD,
      status: ClientStatus.END,
    });
  } catch (error) {}

  // 曝光
  let isShow = false;
  bridge.registerLifecycleListener({
    success(res) {
      const { lifeStatus } = res.data;
      if (lifeStatus === 'showEnd' && !isShow) {
        console.log('registerLifecycleListener call success');
        ksbLog({
          actionType: AdActionType.AD_ITEM_PLAY_END_CARD_IMPRESSION,
          payload: {
            clientExtData: JSON.stringify({
              template_id: matrixLayout.styleId,
            }),
          },
        });
        radarEnd('impressionRate');
        radarStart('convertRate');
        radarInfo('impressionRateSuccess');
        isShow = true;
        clientLog({
          event: ClientEvent.PAGE_IMPRESSION,
          scene_id: AdStyle[info.adStyle],
          page_type: PageType.ENDCARD,
          status: ClientStatus.END,
        });
      }
    },
  });

  render(
    <>
      <Root schema={layout} info={info} components={components} page={page} />
    </>,
    document.getElementById('app'),
    () => {
      try {
        changePageStatus(PageStatus.RENDER_COMPLETE); // 渲染成功
        radarFmp();
        // styleId 挂载
        const styleId = json?.styleId || '';
        setQueryString('styleId', styleId);
      } catch (e) {
        console.log(e);
      }
    },
  );
};

main();
