import { ItemClickType } from '@enum';
import { CommonProps } from '@interface';
import classNames from 'classnames';
import { RootStoreContext } from 'context/StoreContext';
import { FC, useContext } from 'react';
import './index.less';

const Card: FC<CommonProps> = (props = {}) => {
  const {
    schema: {
      props: { block = false } = {},
      children = [],
      onClick = () => {},
      logInfo: { itemClickType = ItemClickType.CARD_BLANK_CLICK } = {},
    },
    style = {},
    render,
  } = props;

  const { page: { cardBgColor = '' } = {} } = useContext(RootStoreContext);

  const cardStyle = {
    ...style,
    background: cardBgColor || 'white',
  };

  return (
    <div
      className={classNames('card', { block })}
      onClick={onClick}
      style={cardStyle}
      data-itemClickType={itemClickType}
    >
      {Array.isArray(children) && children.length
        ? children.map((item) => {
            return render({ node: item });
          })
        : null}
    </div>
  );
};

export default Card;
