import * as componentLib from 'componentLib';
import { Suspense } from 'react';
import { styleHandle } from '../utils';
import { renderChild } from './RootRenderer';

type DynamicType = {
  schema: any;
  info: any;
  // componentsType?: any;
};

const DynamicEngine = (props?: DynamicType) => {
  const { schema = {}, info = {} } = props;
  const { name = '', props: prop = {}, style = {}, ...rest } = schema;
  // scheme中其余属性可以留存到props对象中
  schema.props = { ...rest, ...prop };

  const caleStyle = styleHandle(style, {});

  // 不存在type
  if (!name) return <></>;

  const Component = componentLib[name];

  return (
    <Suspense fallback={<></>}>
      <Component schema={schema} info={info} style={caleStyle} render={renderChild} />
    </Suspense>
  );
};

export default DynamicEngine;
