import { CommonProps } from '@interface';
import * as componentLib from 'componentLib';
import { FC, Suspense } from 'react';
import { styleHandle } from '../utils';
import { renderChild } from './RootRenderer';

const Slot: FC<CommonProps> = (props = {}) => {
    const { schema = {}, info = {}, style } = props;
    const { name, style: sStyle, animationType } = schema;
    const slotStyle = styleHandle(style, sStyle);

    if (!name) return <></>;
    const Component = componentLib[name];
    return (
        <Suspense fallback={<></>}>
            <Component
                schema={schema}
                info={info}
                style={slotStyle}
                render={renderChild}
                animationType={animationType}
            />
        </Suspense>
    );
};

export default Slot;
