import React, { useCallback } from 'react';
import lottie, { AnimationItem } from 'lottie-web/build/player/lottie_svg';
import leftRightSlide from './left-right-slide.json';
import './index.less';

const LeftRightSlide = () => {
  let leftRightSlideAnim: AnimationItem;

  const leftRightEl = useCallback((node) => {
    if (node === null) return;
    leftRightSlideAnim = lottie.loadAnimation({
      container: node,
      renderer: 'svg',
      animationData: leftRightSlide,
      loop: true,
      autoplay: false,
    });
    setTimeout(() => {
      leftRightSlideAnim.play();
    }, 500);
    leftRightSlideAnim.setSubframe(false);
  }, []);
  return <div className="left-right-anim" ref={leftRightEl}></div>;
};
export default LeftRightSlide;
