import React, { FC } from 'react';
import { CommonProps } from '@interface';
import { ItemClickType } from '@enum';
import './index.less';

const Image: FC<CommonProps> = (props = {}) => {
    const {
        schema: {
            className = '',
            url,
            key = 'imageUrl',
            logInfo: { itemClickType } = { itemClickType: '' },
        },
        style = {},
        info: { [key]: imageUrl },
    } = props;
    const renderUrl = url || imageUrl;
    return renderUrl ? (
        <img
            data-itemClickType={itemClickType}
            src={renderUrl}
            className={`${className}`}
            style={style}
        />
    ) : null;
};

export default Image;
