import { ItemClickType } from '@enum';
import classNames from 'classnames';
import { useState } from 'react';
import './index.less';

const Guide = (props) => {
  const [isShowGuide, setIsShowGuide] = useState(true);
  const { render, isShow, isPorttrait } = props;
  if (isShow) {
    return (
      <div
        onTouchEnd={() => {
          setIsShowGuide(false);
        }}
        className={classNames(
          'guide-wrap',
          { horizontal: !isPorttrait },
          { 'hide-mask': !isShowGuide },
        )}
        data-itemClickType={ItemClickType.PLAY_END_CLICK2}
      >
        {render({
          node: {
            name: 'Guide',
          },
        })}
      </div>
    );
  }
  return <></>;
};
export default Guide;
