import { CommonProps } from '@interface';
import classNames from 'classnames';
import { styleHandle } from 'utils';
import './index.less';

export default function Tags(props: CommonProps) {
  const {
    schema: {
      props: {
        mode = 'normal', // normal || custom
        match = 'gray', // orange white gray font-orange
        color = '',
        backgroundColor = '',
        borderColor = '',
      } = {},
      logInfo: { itemClickType = '' } = {},
    },
    style = {},
    info: { exposeTagInfoList = [] },
  } = props;

  const tagList = exposeTagInfoList.map((item) => item?.text);
  if (mode === 'custom') {
    return (
      <>
        {tagList.length ? (
          <div
            className={classNames('tags-cus-list')}
            data-itemClickType={itemClickType}
            style={style}
          >
            {tagList.map((item) => (
              <span className="tag" key={item}>
                {item}
              </span>
            ))}
          </div>
        ) : null}
      </>
    );
  }
  return (
    <>
      {tagList.length ? (
        <div
          className={classNames('tags-list', `tags-${match}`)}
          data-itemClickType={itemClickType}
          style={style}
        >
          {tagList.map((item) => (
            <span
              className="tag"
              key={item}
              style={styleHandle({ color, backgroundColor, borderColor })}
            >
              {item}
            </span>
          ))}
        </div>
      ) : null}
    </>
  );
}
