import { ItemClickType } from '@enum';
import { CommonProps, DownloadInfoProps } from '@interface';
import btnHand from 'assets/imgs/btn-hand.png';
import classNames from 'classnames';
import { FC } from 'react';
import './index.less';
import { useDownloadInfo } from './useDownloadInfo';

const Buttons: FC<CommonProps> = (props = {}) => {
  const {
    schema: {
      className = '',
      props: {
        mode = 'default', // default linear
        shape = 'round', // r6 r12
        downloadingFormat = '%',
        downloadPauseFormat = '%',
        color = 'white;white',
        backgroundColor = '#FF5000;#2E53EB',
        animateType = '',
      } = {},
    },
    style = {},
    info: {
      text = {
        adActionDescription: '',
        openAppLabel: '',
        installAppLabel: '',
      },
      leftButtonTexts = {
        adActionDescription: '',
        openAppLabel: '',
        installAppLabel: '',
      },
      isDownload = false,
    },
  } = props;
  const splitProps = (value: string) => {
    const values = value.split(';');
    return values;
  };

  const downloadInfoOptions: DownloadInfoProps = {
    texts: text,
    isDownload,
  };

  if (downloadingFormat) {
    downloadInfoOptions.downloadingFormat = downloadingFormat;
  }
  if (downloadPauseFormat) {
    downloadInfoOptions.downloadPauseFormat = downloadPauseFormat;
  }

  const { btnText } = useDownloadInfo(downloadInfoOptions);
  const [leftColor, rightColor] = splitProps(color);
  const [leftBgColor, rightBgColor] = splitProps(backgroundColor);

  return (
    <div className="btn-wrap" style={style}>
      <button
        type="button"
        data-itemClickType={ItemClickType.PLAY_END_CLICK1}
        className={classNames('multi-btn left-btn', `shape-${shape}`, `mode-${mode}`, className)}
        style={{
          color: leftColor,
          background: leftBgColor,
        }}
      >
        <span>{leftButtonTexts.adActionDescription}</span>
      </button>
      <button
        type="button"
        data-itemClickType={ItemClickType.PLAY_END_CLICK2}
        className={classNames(
          'multi-btn right-btn',
          `shape-${shape}`,
          `mode-${mode}`,
          className,
          animateType,
        )}
        style={{
          color: rightColor,
          background: rightBgColor,
        }}
      >
        <span>{btnText}</span>
        <img src={btnHand} alt="小手" className="hand" />
      </button>
    </div>
  );
};

export default Buttons;
