import { clickAction } from '@api';
import { ItemClickType, traceToAreaMap } from '@enum';
import { PageProps } from '@interface';
import { RootStoreContext } from 'context/StoreContext';
import { Schema } from '../types';
import { cardToEndMap } from '../utils';
import RootRenderer from './RootRenderer';

export function Root({ schema, info, components, page }: PageProps<Schema>) {
  const RootStore = {
    info,
    components: components.filter((item) => item.type !== 'inner'),
    innerComponents: components.filter((item) => item.type === 'inner'),
    page,
    index: 0,
    getCmp: function () {
      return RootStore.components[RootStore.index++];
    },
    getInnerCmp(name: string) {
      return RootStore.innerComponents.find((item) => item.name == name);
    },
  };
  let slided = false;
  const touchEnd = (e) => {
    let target = (e.target || e.currentTarget) as HTMLElement;
    if (slided) {
      if (!info.slideClick) {
        slided = false;
        return false;
      }
      const itemClickType = target.dataset.itemclicktype;
      clickAction({
        area: traceToAreaMap(itemClickType),
        e,
        payload: {
          itemClickType: ItemClickType.CAED_SKIDE_CLICK,
        },
      });
      slided = false;
    } else {
      slided = false;
      let itemClickType;
      //   let unConvert;
      //   let type;
      try {
        while (target) {
          if (target.getAttribute) {
            itemClickType = target.dataset.itemclicktype;
            // unConvert = target.dataset.unconvert;
          }
          if (itemClickType) {
            // type = itemClickType;
            target = null;
          } else if (target.parentNode) {
            target = target.parentNode as HTMLElement;
          } else {
            target = null;
          }
        }
        clickAction({
          area: traceToAreaMap(itemClickType),
          e,
          payload: {
            itemClickType: cardToEndMap[itemClickType]
              ? cardToEndMap[itemClickType]
              : itemClickType,
          },
        });
      } catch (err) {
        console.error(err);
      }
    }
  };
  const touchMove = () => {
    if (!slided) {
      slided = true;
    }
  };
  return (
    <RootStoreContext.Provider value={RootStore}>
      <div onTouchEnd={touchEnd} onTouchMove={touchMove} data-itemClickType={12}>
        <RootRenderer schema={schema} />
      </div>
    </RootStoreContext.Provider>
  );
}
