import { CommonProps } from '@interface';
import classNames from 'classnames';
import { RootStoreContext } from 'context/StoreContext';
import { FC, useContext } from 'react';
import './index.less';

const AdIcon: FC<CommonProps> = (props = {}) => {
  const {
    schema: {
      logInfo: { itemClickType = '' } = {},
      props: { mode = 'gray' } = {}, // white gray white-fill
    },
    style = {},
    info: { adMarkIcon, adGrayMarkIcon, adSourceDescription },
  } = props;
  const isGrayIcon = mode.includes('gray');
  const { page: { adIconType = mode } = {} } = useContext(RootStoreContext);

  return (
    <div
      className={classNames('ad-union-icon', {
        white: adIconType.includes('white'),
        gray: adIconType.includes('gray'),
        fill: adIconType.includes('fill'),
      })}
      data-itemClickType={itemClickType}
      style={style}
    >
      <div
        style={{ backgroundImage: `url(${isGrayIcon ? adGrayMarkIcon : adMarkIcon})` }}
        className="icon"
      />
      {adSourceDescription && <div className="adicon-des">{adSourceDescription}</div>}
    </div>
  );
};

export default AdIcon;
