import lottie, { AnimationItem } from 'lottie-web';
import { useCallback } from 'react';
import './index.less';
import rightSlide from './right-slide.json';

const RightSlide = () => {
  let rightSlideAnim: AnimationItem;
  const rightEl = useCallback((node) => {
    if (node === null) return;
    rightSlideAnim = lottie.loadAnimation({
      container: node,
      renderer: 'svg',
      animationData: rightSlide,
      loop: true,
      autoplay: false,
    });
    setTimeout(() => {
      rightSlideAnim.play();
    }, 500);
    rightSlideAnim.setSubframe(false);
  }, []);
  return <div className="right-anim" ref={rightEl}></div>;
};
export default RightSlide;
