import { CommonProps, DownloadInfoProps } from '@interface';
import btnHand from 'assets/imgs/btn-hand.png';
import classNames from 'classnames';
import { ItemClickType } from 'common/enum';
import { FC } from 'react';
import './index.less';
import { useDownloadInfo } from './useDownloadInfo';

const Button: FC<CommonProps> = (props = {}) => {
  const {
    schema: {
      className = '',
      key = 'texts',
      props: {
        animateType = '', //   跳动；animate-beat  抖动；animate-beat  扫光 ；animate-hand 小手
        block = false, // 父级填充
        shape = 'round', // round r8 r16
        backgroundColor = '#FE3666',
        borderColor = '',
        color = 'white',
        downloadingFormat = 'T...%',
        downloadPauseFormat = 'T%',
        size = 'middle', // small middle large
      } = {},
      logInfo: { itemClickType = ItemClickType.END_CARD_BTN } = {},
    },
    style = {},
    info: {
      [key]: btnTexts = {
        adActionDescription: '',
        openAppLabel: '',
        installAppLabel: '',
      },
      isDownload = false,
    },
  } = props;

  const downloadInfoOptions: DownloadInfoProps = {
    texts: btnTexts,
    isDownload,
  };

  if (downloadingFormat) {
    downloadInfoOptions.downloadingFormat = downloadingFormat;
  }
  if (downloadPauseFormat) {
    downloadInfoOptions.downloadPauseFormat = downloadPauseFormat;
  }

  const { btnText } = useDownloadInfo(downloadInfoOptions);

  return (
    <button
      type="button"
      data-itemClickType={itemClickType}
      className={classNames(
        'btn',
        `size-${size}`,
        { block, [`shape-${shape}`]: !!shape },
        className,
        animateType,
      )}
      style={{ ...style, background: backgroundColor, borderColor, color }}
    >
      <span>{btnText}</span>
      <img src={btnHand} alt="小手" className="hand" />
    </button>
  );
};

export default Button;
