// 整体样式处理，动态px -> rem值，驼峰转换等样式处理
export const styleHandle = (style = {}, attrs?) => {
    const calcStyle = { ...style, ...attrs };
    // const pxRegex = /"[^"]+"|'[^']+'|url\([^\)]+\)|\b(?<!\#)(\d*\.?\d+)(px)?/ig;
    const pxRegex = /"[^"]+"|'[^']+'|url\([^\)]+\)|(\d*\.?\d+)(px)?/gi;
    const opts = {
        viewportWidth: 375,
        unitPrecision: 2,
        rootValue: 0,
        minPixelValue: 1,
    };
    const finalStyle = {};

    const ignoreStyle = [
        'flex',
        'z-index',
        'opacity',
        'background',
        'background-color',
        'color',
        'font-weight',
        'border',
        'border-color',
    ];
    try {
        for (const key in calcStyle) {
            const newKey = humpToUnderline(key);
            let value = calcStyle[key].toString(); // 获取val值
            if (
                ignoreStyle.indexOf(newKey) < 0 && // ignore 掉的不转换
                value.indexOf('%') < 0 &&
                value.indexOf('v') < 0 && // 百分比/px不转换
                !value.includes('rem')
            ) {
                const pxReplaceForVw = createPxReplace(
                    opts.viewportWidth / 10,
                    opts.minPixelValue,
                    opts.unitPrecision,
                    'rem'
                );
                value = value.replace(pxRegex, pxReplaceForVw);
            }

            finalStyle[newKey] = value;
        }
    } catch (e) {
        console.error(e);
        return style;
    }
    return finalStyle;
};

function createPxReplace(perRatio, minPixelValue, unitPrecision, unit) {
    return function (m, $1) {
        if (!$1) return m;
        const pixels = parseFloat($1);
        if (pixels <= minPixelValue) return m;
        return toFixed(pixels / perRatio, unitPrecision) + unit;
    };
}

function toFixed(number, precision) {
    const multiplier = 10 ** (precision + 1);
    const wholeNumber = Math.floor(number * multiplier);
    return (Math.round(wholeNumber / 10) * 10) / multiplier;
}

function humpToUnderline(str = '') {
    return str.replace(/([A-Z])/g, '-$1').toLowerCase();
}

export const cardToEndMap = {
    '29': 26, // actionBar
    '53': 35, // 空白区域点击
    '30': 15, // appIcon
    '31': 16, // title
    '32': 17, // des
    '46': 44, // 下载次数
};
