import { isMock } from '@util';
import { bridge } from '@ad/ks-ad-alliance-bridge';
import mockBridge from './mock';
// 初始化bridge
bridge.config({
    debug: true,
    handler: 'callAdBridge',
});

const initUnionBridge = () => {
    if (isMock) {
        console.warn('~~~mock环境~~~~');
        return mockBridge;
    }
    return bridge;
};

const proxyBridge = initUnionBridge();
export { proxyBridge as bridge };
export * from './method';
