import { RootStoreContext } from 'context/StoreContext';
import { useContext } from 'react';
import DynamicEngine from './DynamicEngine';
import Slot from './Slot';

export default function RootRenderer(props: any) {
  const { schema } = props;
  return <>{renderChild({ node: schema })}</>;
}

export function renderChildren({ node }) {
  if (Array.isArray(node)) {
    return node.map((node) => renderChild({ node }));
  }
}

// 渲染组件
export function renderChild({ node }) {
  if (Array.isArray(node)) {
    return renderChildren({ node });
  }
  const typeofnode = typeof node;
  if (typeofnode === 'undefined' || node === null) {
    return null;
  }
  const schema = node;
  const { name = '' } = schema;

  if (name === 'Slot') {
    return renderSlot(schema);
  }

  const { info = {} } = useContext(RootStoreContext);
  return <DynamicEngine schema={schema} info={info} />;
}

function renderSlot(schema) {
  const { info = {}, getCmp } = useContext(RootStoreContext);
  const slotSchema = getCmp();
  if (!slotSchema) {
    return <></>;
  }
  return <Slot schema={slotSchema} info={info} style={schema.style} />;
}
