import { CommonProps } from '@interface';
import classNames from 'classnames';
import { RootStoreContext } from 'context/StoreContext';
import { FC, useContext } from 'react';
import ClickRipple from './Gesture/ClickRipple';
import LeftRightSlide from './Gesture/LeftRightSlide';
import RightSlide from './Gesture/RightSlide';
import SlideParallel from './Gesture/SlideParallel';
import TopArrow from './Gesture/TopArrow';
import TouchId from './Gesture/TouchId';
import './index.less';

const Guide: FC<CommonProps> = (props) => {
  const {
    schema: {
      preview = false,
      props: {
        type: schemaType = '', // right_slide
      } = {},
    },
    info: {
      texts = {
        adActionDescription: '',
        openAppLabel: '',
        installAppLabel: '',
      },
    },
  } = props;

  const { getInnerCmp = () => ({}) } = useContext(RootStoreContext);
  const { props: { type: innerType = 'right_slide' } = {} } = getInnerCmp('Guide') || {};

  const type = schemaType || innerType || 'right_slide';

  const animList = [
    {
      id: 'right_slide',
      dom: <RightSlide />,
      text: '向右滑动',
    },
    {
      id: 'left_right_slide',
      dom: <LeftRightSlide />,
      text: '左右晃动',
    },
    {
      id: 'left_slide_parallel',
      dom: <SlideParallel direction="left" />,
      text: '向左滑动',
    },
    {
      id: 'right_slide_parallel',
      dom: <SlideParallel direction="right" />,
      text: '向右滑动',
    },
    {
      id: 'top_slide_parallel',
      dom: <SlideParallel direction="top" />,
      text: '向上滑动',
    },
    {
      id: 'top_arrow',
      dom: <TopArrow />,
      text: '向上滑动',
    },
    {
      id: 'click_small',
      dom: <ClickRipple size="small" />,
      text: '点击任意',
    },
    {
      id: 'click_big',
      dom: <ClickRipple size="big" />,
      text: '点击任意',
    },
    {
      id: 'touch_id',
      dom: <TouchId isTouched={false} />,
      text: '点击任意',
    },
  ];

  const animResult = animList.find((item) => item.id === type);

  return (
    <div className={classNames('anim-box', { preview: preview })}>
      {animResult.dom}
      <div className={`title`}>
        <h1>{animResult.text}</h1>
        <h2>{texts.adActionDescription}</h2>
      </div>
    </div>
  );
};
export default Guide;
