import { getQueryString } from '@util';

export function error(message: string, args: { [key: string]: any } = {}) {
    const env = getQueryString('env');
    try {
        if (env !== 'log' && args.type === 'bridge') {
            return false;
        } 
        const error = new Error(
            JSON.stringify({
                name: message,
                message: JSON.stringify(args),
                event_type: 'error',
                src: window.location.href,
            })
        );
        window.weblog.captureException(error);
        
    } catch (error) {
        console.log(error);
    }
}
