import { bridge } from '@bridge';
import {
  BridgeCallbackResponse,
  BridgeClickActionProps,
  ClickActionProps,
  LogAndConvertProps,
  LogParamsType,
  ClientMSG,
} from '@interface';
import { error } from '@log';
import { radarEnd } from '@util';

export function changePageStatus(status) {
  try {
    bridge.pageStatus({ status });
  } catch (e: any) {
    error('pageStatus fail', { type: 'bridge' });
  }
}

export const ksbLog = async (
  params: LogParamsType,
): Promise<void | BridgeCallbackResponse<null>> => {
  let payload = '{}';
  if (params.payload) {
    payload = JSON.stringify(params.payload);
  }
  console.log('广告埋点:', {
    actionType: params.actionType,
    payload,
    clickInfo: params.clickInfo,
  });
  bridge
    .log({
      actionType: params.actionType,
      payload,
      clickInfo: params.clickInfo,
    })
    .catch(() => {
      error('log fail', { params, type: 'bridge' });
    });
};

export const clickAction = ({
  unlog,
  area = 1,
  e,
  payload = {},
  adTemplate,
  refreshType,
  creativeId,
  callback,
  needReport = true,
  containerWidth = window.screen.width,
  containerHeight = window.screen.height,
  convertType,
}: ClickActionProps) => {
  radarEnd('convertRate');
  if (unlog === 'true') {
    return;
  }
  const clickActionButton = area === 1;
  const clickInfo = {
    x: e?.pageX,
    y: e?.pageY,
    width: containerWidth,
    height: containerHeight,
  };
  const logParam = {
    actionType: 2,
    clickInfo,
    payload: JSON.stringify(payload),
    refreshType,
  };
  const clickActionProps: BridgeClickActionProps = {
    area,
    logParam,
    needReport,
    convertType,
  };
  if (adTemplate) {
    clickActionProps.adTemplate = adTemplate;
  }
  if (creativeId) {
    clickActionProps.creativeId = creativeId;
  }
  console.log(clickActionProps);
  bridge
    .clickAction(clickActionProps)
    .then(() => {
      callback && callback();
    })
    .catch(() => {
      const logAndConvertProps: LogAndConvertProps = {
        clickInfo,
        payload: JSON.stringify(payload),
        clickActionButton,
        callback,
      };
      if (adTemplate) {
        logAndConvertProps.adTemplate = adTemplate;
      }
      if (refreshType) {
        logAndConvertProps.refreshType = refreshType;
      }
      ksbLog({
        actionType: 2,
        clickInfo,
        payload,
        adTemplate,
        refreshType,
      });
      bridge
        .convert({
          clickActionButton,
          adTemplate,
        })
        .catch(() => {
          error('clickAction fail', { type: 'bridge' });
        });
    });
};

export const nativeConfig = {
  biz: 'AD_WEBVIEW',
  sub_biz: '',
  event_id: 'ad_sdk_webview_track',
  type: 'EXECUTION_BIZ_TIME',
  tag: 'ad_sdk_webview_track',
  //ad_client_apm_log 非实时上报  ad_client_error_log 实时上报
  category: 'ad_client_apm_log',
  rate: 0.1,
};

export const clientLog = async (msg: ClientMSG) => {
  try {
    const params = {
      msg: {
        event: msg.event, // 事件名
        status: msg.status, // 100
        url: location.href, // h5地址
        scene_id: msg.scene_id, //大业务场景
        page_type: msg.page_type, //具体的卡片类别
      },
      ...nativeConfig,
    };
    console.log(`commercialLog:` + JSON.stringify(params));
    await bridge.commercialLog(params);
  } catch (error) {}
};
